import React from 'react'
import { graphql } from 'gatsby'
import Helmet from 'react-helmet'

import Layout from '../components/layout'
import * as toc from '../../left-toc.json';
import { push } from "../../node_modules/gatsby-link/index";
import { isApi } from '../templates/site-config';

var routes = toc.path;

class BlogIndex extends React.Component {
  componentDidMount() {
    if (isApi) {
      var pathSplit = routes[0].split('/');
      pathSplit.pop();
      push(pathSplit.join('/'));
    } else {
      push(routes[0]);
    }
  }

  render() {
    return (
      <Layout location={this.props.location}>
        <Helmet
          htmlAttributes={{ lang: 'en' }}
          meta={[{ name: 'description', content: 'Syncfusion ' }]}
          title={'Essential JS 2'}
        />
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
query {
  allMarkdownRemark {
    edges { 
      node {
        fields {
          slug
        }
      }
    }
  }
}
`
